.footer{
    background-color: #74b9ff; /*var(--container-color); */
    border-top: 1px soli rgba(0, 0, 0, 0.1);
}
.footer__container{
    padding: 1rem 0 2rem;
}
.footer__title,
.footer__link{
    color: var(--title-color);
}
.footer__title{
    text-align: center;
    margin-bottom: var(--mb-1);
}
.footer__link:hover{
    color:var(--title-color-dark);
}
.footer__list{
    display: flex;
    justify-content: center;
    column-gap: 1.5rem;
    margin-bottom: var(--mb-1);
}
.footer__social{
    display: flex;
    justify-content: center;
    column-gap: 1.25rem;
}
.footer__social-link{
    background-color: black;
    color: var(--container-color);
    border: 1px solid black;
    font-size: 1.25rem;
    padding: 0.4rem;
    border-radius : 0.5rem;
    display: inline-flex;
}
.footer__social-link:hover{
    border: 1px solid #dfe6e9;
    background-color: #74b9ff;
}
.footer__copy{
    display: block;
    margin-top: 4.5rem;
    color: var(--title-color);
    text-align: center;
    font-size: var(--small-font-size);
}

/*=============== BREAKPOINTS ===============*/
/* For large devices */
@media screen and (max-width: 992px) {
    .footer__social-link{
        padding: 0.25rem;
        border-radius: 0.25rem;
        font-size: 1rem;
    }
  }
